<script>
import Header from "@/components/Header2/header2.vue";
import Footer from "@/components/Footer/footer.vue";
import configSetting from "../../../../public/config/customSetting.json";

export default {
  name: "change-pswd-component",
  components: {
    Header,
    Footer,
  },
  data() {
    // 檢查欄位必填
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("請輸入密碼"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkPass");
        }
        callback();
      }
    };
    // 檢查密碼是否一致
    var validateRePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("請再次輸入密碼"));
      } else if (value !== this.form.p) {
        callback(new Error("兩次密碼輸入不正確!"));
      } else {
        callback();
      }
    };
    return {
      footerHeight: 0,
      headerHeight: 0,
      timeOut: false,
      form: {
        v: "",
        p: "",
        checkPass: "",
      },
      rules: {
        p: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validateRePass, trigger: "blur" }],
      },
    };
  },
  computed: {
    formHeight() {
      return {
        height: `calc(100vh - ${this.headerHeight + this.footerHeight}px)`,
      };
    },
  },
  methods: {
    submit(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          localStorage.clear();
          let p4use = {};
          let acnt = "account";
          let v1 = "12800225;evendor_support@innolux.com";
          let pswd = "password";
          let v2 = "1QaX2WsX";
          let apid = "app_id";
          let v3 = configSetting.APP_ID;

          p4use[acnt] = v1;
          p4use[pswd] = v2;
          p4use[apid] = v3;
          let params = p4use;
          this.getToken(params);
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料輸入不完整",
          });
        }
      });
    },
    getToken(params) {
      this.axios({
        url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
        method: "POST",
        data: params,
        headers: {
          "content-type": "application/json",
        },
      }).then((d) => {
        if (!d.data.data) return;
        this.changePswd();
      });
    },
    async changePswd() {
      try {
        await this.$store.dispatch("changePswd", this.form);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "更改密碼成功",
        });
        this.timeOut = false;
      } catch (err) {
        console.log(err);
        this.timeOut = true;
      }
    },
  },
  mounted() {
    this.$root.$children[0].sidebarSwitch = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);

    this.headerHeight = document.querySelector(".header").offsetHeight;
    this.footerHeight = document.querySelector(".footer").offsetHeight;

    this.form.v = this.$route.query.v;
  },
};
</script>

<template src="./changePswd.html"></template>
<style lang="sass" scoped src="./changePswd.sass"></style>
